module.exports = {"title":"Cascading effects","__content":"<p>One cool feature of <code>react-reveal</code> is the ability to easily create cascading reveal effects. It can be handy if you want animate some boring looking lists. Say you have list like this:</p>\n<pre><code class=\"lang-jsx\">&lt;ul className=&quot;some-class&quot;&gt;\n  &lt;li&gt;First Item&lt;/li&gt;\n  &lt;li&gt;Another Item&lt;/li&gt;\n  &lt;li&gt;Last Item&lt;/li&gt;\n&lt;/ul&gt;\n</code></pre>\n<p>You can liven it up a bit by using <code>cascade</code> prop:</p>\n<pre><code class=\"lang-jsx\">import Fade from &#39;react-reveal/Fade&#39;; \n\n&lt;Fade top cascade&gt;\n  &lt;ul className=&quot;some-class&quot;&gt;\n    &lt;li&gt;First Item&lt;/li&gt;\n    &lt;li&gt;Another Item&lt;/li&gt;\n    &lt;li&gt;Last Item&lt;/li&gt;\n  &lt;/ul&gt;\n&lt;/Fade&gt;\n</code></pre>\n<p>You can also cascade reveal strings of text ( such as headings and so on ):</p>\n<pre><code class=\"lang-jsx\">import Fade from &#39;react-reveal/Fade&#39;; \n\n&lt;Fade top cascade&gt;\n  &lt;h1&gt;Text to reveal&lt;/h1&gt;\n&lt;/Fade&gt;\n</code></pre>\n<p>Every letter will be revealed separately in a cascade.</p>\n<p>Note that the <code>duration</code> prop will be doubled for the cascade effects. So the by default the overall duration will be 2000 milliseconds. If you set <code>duration</code> prop to 3000 milliseconds and enable cascade then the actual duration will be 6000 milliseconds.</p>\n"}

