module.exports = {"title":"Common Effects","__content":"<p>Here is the list of different effects reveal effects. Every component can have a number of different attributes (like left or right versions). See <a href=\"/examples/common/\">examples</a> for details. You can also use the rest of component <a href=\"/docs/props/\">props</a>.</p>\n<h3 id=\"fade\">Fade</h3>\n<p><a href=\"/examples/common/Fade\">Live demo</a></p>\n<pre><code class=\"lang-jsx\">import Fade from &#39;react-reveal/Fade&#39;;\n\n&lt;Fade&gt;\n  &lt;h1&gt;\n    Your content goes here\n  &lt;/h1&gt;\n&lt;/Fade&gt;\n</code></pre>\n<h3 id=\"flip\">Flip</h3>\n<p><a href=\"/examples/common/Flip\">Live demo</a></p>\n<pre><code class=\"lang-jsx\">import Flip from &#39;react-reveal/Flip&#39;;\n\n&lt;Flip&gt;\n  &lt;h1&gt;\n    Your content goes here\n  &lt;/h1&gt;\n&lt;/Flip&gt;\n</code></pre>\n<h3 id=\"rotate\">Rotate</h3>\n<p><a href=\"/examples/common/Rotate\">Live demo</a></p>\n<pre><code class=\"lang-jsx\">import Rotate from &#39;react-reveal/Rotate&#39;;\n\n&lt;Rotate&gt;\n  &lt;h1&gt;\n    Your content goes here\n  &lt;/h1&gt;\n&lt;/Rotate&gt;\n</code></pre>\n<h3 id=\"zoom\">Zoom</h3>\n<p><a href=\"/examples/common/Zoom\">Live demo</a></p>\n<pre><code class=\"lang-jsx\">import Zoom from &#39;react-reveal/Zoom&#39;;\n\n&lt;Zoom&gt;\n  &lt;h1&gt;\n    Your content goes here\n  &lt;/h1&gt;\n&lt;/Zoom&gt;\n</code></pre>\n<h3 id=\"bounce\">Bounce</h3>\n<p><a href=\"/examples/common/Bounce\">Live demo</a></p>\n<pre><code class=\"lang-jsx\">import Bounce from &#39;react-reveal/Bounce&#39;;\n\n&lt;Bounce&gt;\n  &lt;h1&gt;\n    Your content goes here\n  &lt;/h1&gt;\n&lt;/Bounce&gt;\n</code></pre>\n<h3 id=\"slide\">Slide</h3>\n<p><a href=\"/examples/common/Slide\">Live demo</a></p>\n<pre><code class=\"lang-jsx\">import Slide from &#39;react-reveal/Slide&#39;;\n\n&lt;Slide&gt;\n  &lt;h1&gt;\n    Your content goes here\n  &lt;/h1&gt;\n&lt;/Slide&gt;\n</code></pre>\n<h3 id=\"roll\">Roll</h3>\n<p><a href=\"/examples/common/Roll\">Live demo</a></p>\n<pre><code class=\"lang-jsx\">import Roll from &#39;react-reveal/Roll&#39;;\n\n&lt;Roll&gt;\n  &lt;h1&gt;\n    Your content goes here\n  &lt;/h1&gt;\n&lt;/Roll&gt;\n</code></pre>\n<h3 id=\"lightspeed\">LightSpeed</h3>\n<p><a href=\"/examples/common/LightSpeed\">Live demo</a></p>\n<pre><code class=\"lang-jsx\">import LightSpeed from &#39;react-reveal/LightSpeed&#39;;\n\n&lt;LightSpeed&gt;\n  &lt;h1&gt;\n    Your content goes here\n  &lt;/h1&gt;\n&lt;/LightSpeed&gt;\n</code></pre>\n<h2 id=\"unwanted-scrollbars\">Unwanted Scrollbars</h2>\n<p>Please note that some reveal effects might create unwanted scrollbars for a short time. \nIf you want to avoid it set <code>overflow</code> attribute of a containing div to <code>hidden</code>.</p>\n<pre><code class=\"lang-jsx\">&lt;div style={{overflow: &#39;hidden&#39;}}&gt;\n  &lt;Fade right big&gt;\n    &lt;p&gt;Markup that will be revealed on scroll&lt;/p&gt;\n  &lt;/Fade&gt;\n&lt;/div&gt;\n</code></pre>\n<p>You can also do it using CSS.</p>\n"}

