module.exports = {"title":"Custom CSS effects","__content":"<p>If you want to use a custom CSS effect ( from animate.css for example) you&#39;ll need to import the <code>Reveal</code> class first.</p>\n<pre><code class=\"lang-javascript\">import Reveal from &#39;react-reveal/Reveal&#39;;\n</code></pre>\n<p>Then set the <code>effect</code> prop to a desired CSS animation class name. </p>\n<pre><code class=\"lang-jsx\">&lt;Reveal effect=&quot;fadeInUp&quot;&gt;\n  &lt;p&gt;Markup that will be revealed on scroll&lt;/p&gt;\n&lt;/Reveal&gt;\n</code></pre>\n<p>You may also specify leaving animation class using <code>effectOut</code> prop. </p>\n<pre><code class=\"lang-jsx\">&lt;Reveal effect=&quot;fadeInUp&quot; effectOut=&quot;fadeOutLeft&quot;&gt;\n  &lt;p&gt;Markup that will be revealed on scroll&lt;/p&gt;\n&lt;/Reveal&gt;\n</code></pre>\n<p><code>react-reveal</code> overrides a duration and delay of a custom effect (sets it to 1000 milliseconds by default) so you would need set the <code>duration</code> and <code>delay</code> prop if you want to customize it.</p>\n<p>You can also use the rest of the <a href=\"/docs/props\">props</a>. In addition there are out versions of some props: <code>durationOut</code>, <code>delayOut</code>, <code>countOut</code>, <code>foreverOut</code></p>\n<p>Have a look at this <a href=\"/examples/common/custom/\">example</a>.</p>\n"}

