module.exports = {"title":"Introduction","__content":"<p><a href=\"https://github.com/rnosov/react-reveal\">React Reveal</a> is\nan animation framework for React. It&#39;s MIT licensed, has a tiny footprint\nand written specifically for React in ES6. It can be used to create various cool reveal\non scroll animations in your application.\nIf you liked this package, don&#39;t forget to star\nthe <a href=\"https://github.com/rnosov/react-reveal\">Github repository</a>.</p>\n<h2 id=\"live-examples\">Live Examples</h2>\n<p>A number of simple effect examples:</p>\n<ul>\n<li><a href=\"/examples/common/fade/\">Fade</a></li>\n<li><a href=\"/examples/common/flip/\">Flip</a></li>\n<li><a href=\"/examples/common/rotate/\">Rotate</a></li>\n<li><a href=\"/examples/common/zoom/\">Zoom</a></li>\n<li><a href=\"/examples/common/bounce/\">Bounce</a></li>\n<li><a href=\"/examples/common/roll/\">Roll</a></li>\n</ul>\n<p>Also, there are more complicated examples of <a href=\"/examples/advanced/form/\">animated form errors</a> and a <a href=\"/examples/advanced/todo/\">todo app</a>.</p>\n<h2 id=\"installation\">Installation</h2>\n<p>In the command prompt run:</p>\n<pre><code class=\"lang-sh\">npm install react-reveal --save\n</code></pre>\n<p>Alternatively you may use <code>yarn</code>:</p>\n<pre><code class=\"lang-sh\">yarn add react-reveal\n</code></pre>\n<h2 id=\"quick-start\">Quick Start</h2>\n<p>Import effects from <a href=\"https://www.npmjs.com/package/react-reveal\">React Reveal</a> to your project. Lets try <code>Zoom</code> effect first:</p>\n<pre><code class=\"lang-javascript\">import Zoom from &#39;react-reveal/Zoom&#39;;\n</code></pre>\n<p>Place the following code somewhere in your <code>render</code> method: </p>\n<pre><code class=\"lang-jsx\">&lt;Zoom&gt;\n  &lt;p&gt;Markup that will be revealed on scroll&lt;/p&gt;\n&lt;/Zoom&gt;\n</code></pre>\n<p>You should see zooming animation that reveals text inside the tag. You can change this text to any JSX you want. If you place this code further down the page you&#39;ll see that it&#39;d appear as you scroll down.</p>\n<h2 id=\"revealing-react-components\">Revealing React Components</h2>\n<p>You may just wrap your custom React component with the effect of your choosing like so:</p>\n<pre><code class=\"lang-jsx\">&lt;Zoom&gt;  \n  &lt;CustomComponent /&gt;\n&lt;/Zoom&gt;\n</code></pre>\n<p>In such case, in the resulting <code>&lt;CustomComponent /&gt;</code> HTML markup will be wrapped in a <code>div</code> tag. If you would rather have a different HTML tag then wrap <code>&lt;CustomComponent /&gt;</code> in a tag of your choosing:</p>\n<pre><code class=\"lang-jsx\">&lt;Zoom&gt;\n  &lt;section&gt;\n    &lt;CustomComponent /&gt;   \n  &lt;/section&gt;\n&lt;/Zoom&gt;\n</code></pre>\n<p>or if you want to customize <code>div</code> props:</p>\n<pre><code class=\"lang-jsx\">&lt;Zoom&gt;\n  &lt;div className=&quot;some-class&quot;&gt;\n    &lt;CustomComponent /&gt;   \n  &lt;/div&gt;\n&lt;/Zoom&gt;\n</code></pre>\n<h2 id=\"revealing-images\">Revealing Images</h2>\n<p>If you want to reveal an image you can wrap <code>img</code> tag with with the desired <code>react-reveal</code> effect:</p>\n<pre><code class=\"lang-jsx\">&lt;Zoom&gt;\n  &lt;img height=&quot;300&quot; width=&quot;400&quot; src=&quot;https://source.unsplash.com/random/300x400&quot; /&gt;\n&lt;/Zoom&gt;\n</code></pre>\n<p>It would be a very good idea to specify width and height of any image you wish to reveal.</p>\n<h2 id=\"children\">Children</h2>\n<p><code>react-reveal</code> will attach a reveal effect to each child it gets. In other words,</p>\n<pre><code class=\"lang-jsx\">&lt;Zoom&gt;\n  &lt;div&gt;First Child&lt;/div&gt;\n  &lt;div&gt;Second Child&lt;/div&gt;\n&lt;/Zoom&gt;\n</code></pre>\n<p>will be equivalent to:</p>\n<pre><code class=\"lang-jsx\">&lt;Zoom&gt;\n  &lt;div&gt;First Child&lt;/div&gt;\n&lt;/Zoom&gt;\n&lt;Zoom&gt;\n  &lt;div&gt;Second Child&lt;/div&gt;\n&lt;/Zoom&gt;\n</code></pre>\n<p>If you don&#39;t want this to happen, you should wrap multiple children in a <code>div</code> tag:</p>\n<pre><code class=\"lang-jsx\">&lt;Zoom&gt;\n  &lt;div&gt;\n    &lt;div&gt;First Child&lt;/div&gt;\n    &lt;div&gt;Second Child&lt;/div&gt;\n  &lt;/div&gt;\n&lt;/Zoom&gt;\n</code></pre>\n<h2 id=\"server-side-rendering\">Server Side Rendering</h2>\n<p><code>react-reveal</code> supports server side rendering out of the box. In some cases, when the javascript bundle arrives much later than the HTML&amp;CSS it might cause a flickering. To prevent this <code>react-reveal</code> will not apply reveal effects on the initial load. \nAnother option is to apply gentle fadeout effect on the initial render. You can force it on all <code>react-reveal</code> elements by placing the following code somewhere near the entry point of your app:</p>\n<pre><code class=\"lang-jsx\">import config from &#39;react-reveal/globals&#39;;\n\nconfig({ ssrFadeout: true });\n</code></pre>\n<p>Or you you can do it on a per element basis using <code>ssrFadeout</code> prop:</p>\n<pre><code class=\"lang-jsx\">&lt;Zoom ssrFadeout&gt;&lt;h1&gt;Content&lt;/h1&gt;&lt;/Zoom&gt;\n</code></pre>\n<p>One last option is to use <code>ssrReveal</code> prop. If enabled, this option will suppress both flickering and <code>ssrFadeout</code> effect. The unfortunate drawback of this option is that the revealed content will appear hidden to Googlebot and to anyone with javascript switched off. So it will makes sense for images and/or headings which are duplicated elsewhere on the page.</p>\n<h2 id=\"search-engine-optimisation\">Search Engine Optimisation</h2>\n<p><code>react-reveal</code> is regularly checked against googlebot in the Search Console to make sure that googlebot can see the content in the revealed elements. </p>\n"}

