module.exports = {"title":"Specials","__content":"<p>Simple effects designed to attract attention. The idea behind these effects is to alert a user\nthat something has changed in the application state ( for example a new message has been received ).</p>\n<p>There is a handy prop called <code>spy</code> you can use with these effects. You assign a variable from your component state to this prop and whenever this variable changes a reveal action is performed.</p>\n<p>Have a look at the examples to get the idea on how to use them. Please note, that for most effects to work properly they need to be near the centre of their bounding box. </p>\n<h3 id=\"flash\">Flash</h3>\n<p><a href=\"/examples/specials/flash/\">Live demo</a></p>\n<pre><code class=\"lang-jsx\">import Flash from &#39;react-reveal/Flash&#39;;\n\n&lt;Flash&gt;\n  &lt;h1&gt;\n    Your content goes here\n  &lt;/h1&gt;\n&lt;/Flash&gt;\n</code></pre>\n<h3 id=\"headshake\">HeadShake</h3>\n<p><a href=\"/examples/specials/headshake/\">Live demo</a></p>\n<pre><code class=\"lang-jsx\">import HeadShake from &#39;react-reveal/HeadShake&#39;;\n\n&lt;HeadShake&gt;\n  &lt;h1&gt;\n    Your content goes here\n  &lt;/h1&gt;\n&lt;/HeadShake&gt;\n</code></pre>\n<h3 id=\"jello\">Jello</h3>\n<p><a href=\"/examples/specials/jello/\">Live demo</a></p>\n<pre><code class=\"lang-jsx\">import Jello from &#39;react-reveal/Jello&#39;;\n\n&lt;Jello&gt;\n  &lt;h1&gt;\n    Your content goes here\n  &lt;/h1&gt;\n&lt;/Jello&gt;\n</code></pre>\n<h3 id=\"jump\">Jump</h3>\n<p><a href=\"/examples/specials/jump/\">Live demo</a></p>\n<pre><code class=\"lang-jsx\">import Jump from &#39;react-reveal/Jump&#39;;\n\n&lt;Jump&gt;\n  &lt;h1&gt;\n    Your content goes here\n  &lt;/h1&gt;\n&lt;/Jump&gt;\n</code></pre>\n<h3 id=\"pulse\">Pulse</h3>\n<p><a href=\"/examples/specials/pulse/\">Live demo</a></p>\n<pre><code class=\"lang-jsx\">import Pulse from &#39;react-reveal/Pulse&#39;;\n\n&lt;Pulse&gt;\n  &lt;h1&gt;\n    Your content goes here\n  &lt;/h1&gt;\n&lt;/Pulse&gt;\n</code></pre>\n<h3 id=\"rubberband\">RubberBand</h3>\n<p><a href=\"/examples/specials/rubberband/\">Live demo</a></p>\n<pre><code class=\"lang-jsx\">import RubberBand from &#39;react-reveal/RubberBand&#39;;\n\n&lt;RubberBand&gt;\n  &lt;h1&gt;\n    Your content goes here\n  &lt;/h1&gt;\n&lt;/RubberBand&gt;\n</code></pre>\n<h3 id=\"shake\">Shake</h3>\n<p><a href=\"/examples/specials/shake/\">Live demo</a></p>\n<pre><code class=\"lang-jsx\">import Shake from &#39;react-reveal/Shake&#39;;\n\n&lt;Shake&gt;\n  &lt;h1&gt;\n    Your content goes here\n  &lt;/h1&gt;\n&lt;/Shake&gt;\n</code></pre>\n<h3 id=\"spin\">Spin</h3>\n<p><a href=\"/examples/specials/spin/\">Live demo</a></p>\n<pre><code class=\"lang-jsx\">import Spin from &#39;react-reveal/Spin&#39;;\n\n&lt;Spin&gt;\n  &lt;h1&gt;\n    Your content goes here\n  &lt;/h1&gt;\n&lt;/Spin&gt;\n</code></pre>\n<h3 id=\"swing\">Swing</h3>\n<p><a href=\"/examples/specials/swing/\">Live demo</a></p>\n<pre><code class=\"lang-jsx\">import Swing from &#39;react-reveal/Swing&#39;;\n\n&lt;Swing&gt;\n  &lt;h1&gt;\n    Your content goes here\n  &lt;/h1&gt;\n&lt;/Swing&gt;\n</code></pre>\n<h3 id=\"tada\">Tada</h3>\n<p><a href=\"/examples/specials/tada/\">Live demo</a></p>\n<pre><code class=\"lang-jsx\">import Tada from &#39;react-reveal/Tada&#39;;\n\n&lt;Tada&gt;\n  &lt;h1&gt;\n    Your content goes here\n  &lt;/h1&gt;\n&lt;/Tada&gt;\n</code></pre>\n<h3 id=\"wobble\">Wobble</h3>\n<p><a href=\"/examples/specials/wobble/\">Live demo</a></p>\n<pre><code class=\"lang-jsx\">import Wobble from &#39;react-reveal/Wobble&#39;;\n\n&lt;Wobble&gt;\n  &lt;h1&gt;\n    Your content goes here\n  &lt;/h1&gt;\n&lt;/Wobble&gt;\n</code></pre>\n"}

