module.exports = {"title":"when Prop","__content":"<p>Check the <a href=\"/examples/advanced/form/\">form example</a> to get the idea what it&#39;s all about.</p>\n<p>Perhaps the most interesting <code>react-reveal</code> prop is a <code>when</code> prop. It controls whether the reveal action would be triggered or not. Suppose you have a form field and that might generate error message. You might want to gently ease in the error message in case of an error and ease it out once the error is gone.\n<code>when</code> prop would be a kind of on/off switch that is controlling whether to show error or not.</p>\n<pre><code class=\"lang-jsx\">&lt;Zoom when={false}&gt;\n  &lt;h1&gt;You won&#39;t see me, but I will still take space on screen&lt;/h1&gt;\n&lt;/Zoom&gt;\n\n&lt;Zoom when={true}&gt;\n  &lt;h1&gt;I will be seen&lt;/h1&gt;\n&lt;/Zoom&gt;\n</code></pre>\n<p>In other words <code>when</code> prop is controlling whether to trigger reveal effect or not. By default, <code>when</code> prop is <code>true</code>. Once you use this prop it disables the initial reveal animation. In order to trigger reveal effect you&#39;ll need to change it to an opposing value ( <code>true</code> to <code>false</code> or vice versa ). If you still need the initial animation use <code>appear</code> prop. If you initialize <code>when</code> prop to a falsey value then the element will be hidden until you set <code>when</code> prop to a truthy value.</p>\n<p>The reveal action will occur whenever you change <code>when</code> prop from a falsy to a truthy value. Conversely, the conceal action will take place when you change <code>when</code> prop from a truthy to a falsy value. It probably makes sense to set this prop to a variable from the component state.</p>\n<h2 id=\"collapse\">Collapse</h2>\n<p>Another prop that you&#39;ll probably would want to use with it is <code>collapse</code>. The issue is that in the concealed state ( <code>when={false}</code> ) the element would be still taking valuable screen real estate. The <code>collapse</code> prop would make sure that it won&#39;t be taking space when it&#39;s not in use. This is very useful for all sorts of things like menus, error messages, list items ( especially on mobile or anywhere else where space is an issue ) .</p>\n<pre><code class=\"lang-jsx\">&lt;Zoom when={false} collapse&gt;\n  &lt;h1&gt;You won&#39;t see me, and I will not take space on a screen&lt;/h1&gt;\n&lt;/Zoom&gt;\n</code></pre>\n"}

