module.exports = {"title":"Higher Order Components","__content":"<p>If you ever find yourself constantly keep using same <code>&lt;Fade /&gt;</code>, <code>&lt;Zoom /&gt;</code> and other effects with a particular component you can directly inject <code>react-reveal</code> functionality into the component. You&#39;d\ndo it with a higher order function called <code>withreveal</code>. In order to use it, you have to import it first alongside with a desired effect (will use <code>Fade</code> as example):</p>\n<pre><code class=\"lang-jsx\">import makeCarousel from &#39;react-reveal/makeCarousel&#39;;\nimport Fade from &#39;react-reveal/Fade&#39;;\n</code></pre>\n<p>then use it like so</p>\n<pre><code class=\"lang-jsx\">const NewComponent = withReveal(OldComponent, &lt;Fade left /&gt;);\n</code></pre>\n<p>Now <code>NewComponent</code> will be automatically wrapped with <code>&lt;Fade left /&gt;</code> effect. \nThe good thing about it is that you can now use <code>NewComponent</code> as if it was <code>react-reveal</code> component \n( say in transition groups or carousels ). <code>withReveal</code> will intelligently remap props of <code>NewComponent</code> to make it possible. The following props will be extracted and passed to the chosen <code>react-reveal</code> effect:</p>\n<ul>\n<li>force</li>\n<li>mountOnEnter</li>\n<li>unmountOnExit</li>\n<li>opposite</li>\n<li>mirror</li>\n<li>wait</li>\n<li>onReveal</li>\n<li>in</li>\n<li>when</li>\n<li>spy</li>\n<li>collapse</li>\n<li>onExited</li>\n<li>enter</li>\n<li>exit</li>\n<li>appear</li>\n</ul>\n<p><code>withReveal</code> will wrap your component in a <code>div</code> tag for it to work. If you don&#39;t want that \nthen you can <a href=\"https://reactjs.org/docs/refs-and-the-dom.html#exposing-dom-refs-to-parent-components\">expose DOM ref</a> to <code>react-reveal</code>. You do that by using <code>refProp</code> prop. Consider following custom React Component:</p>\n<pre><code class=\"lang-jsx\">function OldComponent({ innerRef, className, style }) {\n  return (&lt;div ref={innerRef} className={className} style={style}&gt;Some content&lt;/div&gt;);\n}\n</code></pre>\n<p>And then you can inject reveal functionality using following code:</p>\n<pre><code class=\"lang-jsx\">const NewComponent = withReveal(OldComponent, &lt;Fade left refProp=&quot;innerRef&quot; /&gt;);\n</code></pre>\n<p>In this case, <code>react-reveal</code> will not insert any tags and will use the exposed HTML element. Many React components such as, Styled Components or React Router links already expose their refs via <code>innerRef</code> prop.\nStyled Components will be detected automatically and <code>refProp</code> will be set accordingly.</p>\n"}

